@use "base";

.hero,
.newsletter,
.reservation-section,
.pricelist-section,
.akce-section,
.o-kave-section,
.map {
    padding: base.$spacer;
}

.picture__img {
	width: 100%;
	border-radius: base.$rad-tiny;
}

.img__float-left {
    width: 35%;
    float: left;
    margin-right: 20px;
    border-radius: base.$rad-tiny;
}

.img__float-right {
    width: 35%;
    float: right;
    margin-left: 20px;
    border-radius: base.$rad-tiny;
}


@media screen and (min-width: 600px) {

    .hero,
    .newsletter,
    .akce-section__item {
        display: flex;
        gap: 40px;
        justify-content: space-between;
    }

    .content {
        flex: 1 1 60%;
        align-items: start;
    }

    .picture {
        order: -1;
        flex: 1 1 40%;
        align-items: start;
    }

}