@use "base";

.header {
    width: 100%;
    padding: 10px 10px 5px 10px;
    background-color: base.$c-secondary;
    color: base.$c-primary;
    margin-bottom: 5px;
}

.header__logo {
    font-size: 50px;
    text-shadow: 0 3px 10px rgba(0,0,0,0.4);
    margin-top: 0;
}

.header__claim {
    color: base.$c-tertiary;
    font-size: 20px;
    text-shadow: 0 3px 10px rgba(0,0,0,0.4);
    font-style: italic;
}

.header__subtitle {
    font-size: 16px;
    font-style: bold;
    margin: 0;
}

.header__text {
    padding: 0;
    margin: 0 0 10px 0;
}

.menu {
    margin-top: base.$spacer;
    margin: 0;
	padding: 0;
}

.menu__items {
    list-style: none;
    padding: 0;
}

.menu__item {
    background-color: base.$c-secondary;
    color: base.$c-primary;
    border: 1px solid base.$c-secondary; 
    border: 1px solid base.$c-primary;
    border-radius: base.$rad-tiny;
}

.menu__item--active {
    color: base.$c-secondary;
    background-color: base.$c-primary;
    border: 1px solid base.$c-secondary;
}

.menu__link {
    text-decoration: none;
	display: block;
	padding: 10px;
}

.menu__link:hover, 
.menu__link:focus {
	background-color: base.$c-tertiary;
	color: base.$c-secondary;
}

@media screen and (min-width: 600px) {
    .header__logo-claim,
    .header__info {
    display: flex;
    gap: 30px;
    align-items: baseline;
}

    .address,
    .opening-hours {
        display: flex;
        flex-direction: column;
    }

    .menu__items {
        display:flex;
        justify-content: flex-start;
        gap: 10px;
    }

    .menu__item {
        display: inline-block;
    }

    .menu__link {
        padding: 10px 10px;
    }
}