html {box-sizing: border-box; background-color: darkgrey;}
*, ::before, ::after {box-sizing: inherit;}

$c-primary: #980964c5;
$c-secondary: rgb(234, 214, 198);
$c-tertiary: rgba(152, 9, 100, 0.459);
$c-quaternary: rgba(148, 109, 81, 0.703);
$font-normal: Georgia, serif;
$font-special: Georgia, serif;
$size_m: 14px;
$size_l: 16px;
$size_xl: 18px;
$spacer: 20px;
$rad-tiny: 5px;

body {
      font-family: Georgia, serif;
      font-size: $size_l;
      background-color: $c-secondary;
      margin: 0 auto;
      padding: 0;
      max-width: 1000px;
}

.footer-info {
    width: 100%;
    padding: 15px 10px;
    text-align: center;
    background-color: $c-primary;
    color: $c-secondary;
    font-size: $size_m;
}


@media screen and (min-width: 600px) {
    .wrapper {
        display: flex;
    }
    
    .page {
        flex: 1 1 85%;
    }
    
    .aside {
        flex: 0 0 15%;
        background-image: url(/images/bgr-cofee2.png);
        background-position: top;
        background-repeat: repeat;
    }
}