@use "base";

.claim--center {
    margin: 20px auto;
    color: base.$c-primary;
    text-align: center;
}

.pricelist {
    width: 100%;
}

.pricelist td {
    width: 85%;
}

@media screen and (min-width: 600px) {

    .pricelist-section {
        display: flex;
        gap: 40px;
    }

    .pricelist__column {
        flex: 1 1 45%;
        align-items: start;
    }

}